<template>
  <div>
    <router-view></router-view>
    <div id="app"></div>
  </div>
</template>

<script>
  export default {
    name: 'App',
    data: function () {
      return {
        section: 'Head',
        callingAPI: false,
        serverURI: 'http://localhost:8000',
        caller: this.$http
      }
    },
    methods: {
      logout: function () {
        this.$store.dispatch('SET_USER', '')

        if (window.localStorage) {
          window.localStorage.setItem('user', '')
        }

        this.$router.push('/login')
      }
    }
  }
</script>



