import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
import NotFoundView from './components/404.vue'

// Import Views - Dash
import Home from './components/dash/Dashboard.vue'
import TablesView from './components/dash/Tables.vue'
// import TasksView from './components/dash/Tasks.vue'
import SettingView from './components/dash/Setting.vue'
// import AccessView from './components/dash/Access.vue'
import ServerView from './components/dash/Server.vue'
import ReposView from './components/dash/Repos.vue'

import Test from './components/custom/Test.vue'
import Input from './components/custom/Input.vue'
import Laporan from './components/custom/Laporan.vue'

// Routes
const routes = [
  {
    path: '/login',
    component: LoginView
  }, {
    path: '/',
    component: DashView,
    auth: true,
    children: [
      {
        path: '',
        component: Home,
        name: 'Dashboard',
        description: 'Overview of environment'
      }, {
        path: '/tables',
        component: TablesView,
        name: 'Tables',
        description: 'Simple and advance table in CoPilot'
      }, {
        path: '/setting',
        component: SettingView,
        name: 'Settings',
        description: 'User settings page'
      // }, {
      //   path: '/access',
      //   component: AccessView,
      //   name: 'Access',
      //   description: 'Example of using maps'
      }, {
        path: '/server',
        component: ServerView,
        name: 'Servers',
        description: 'List of our servers'
      }, {
        path: '/repos',
        component: ReposView,
        name: 'Repository',
        description: 'List of popular javascript repos'
      },
      {
        path: '/tes',
        component: Test,
        name: 'tes'
      },
      {
        path: '/input',
        component: Input,
        name: 'Input Data'
      },
      {
        path: '/laporan',
        component: Laporan,
        name: 'Laporan Perhitungan'
      }
    ]
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]

export default routes



