<template>
  <div class="container container-table">
      <div class="row vertical-10p">
        <div class="container">
          <img src="/static/img/logo.png" class="center-block logo">
          <div class="text-center col-md-4 col-sm-offset-4">
            <!-- errors -->
            <div v-if=response class="text-red"><p>{{response}}</p></div>

            <!-- login form -->
            <form class="ui form loginForm"  @submit.prevent="checkCreds">

              <div class="input-group">
                <span class="input-group-addon"><i class="fa fa-envelope"></i></span>
                <input class="form-control" name="username" placeholder="Username" type="text" v-model="username">
              </div>

              <div class="input-group">
                <span class="input-group-addon"><i class="fa fa-lock"></i></span>
                <input class="form-control" name="password" placeholder="Password" type="password" v-model="password">
              </div>
              <button type="submit" v-bind:class="'btn btn-primary btn-lg ' + loading">Submit</button>
            </form>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
module.exports = {
  name: 'Login',
  data: function (router) {
    return {
      section: 'Login',
      loading: '',
      username: '',
      password: ''
    }
  },
  methods: {
    checkCreds: function () {
      var store = this.$store
      this.toggleLoading()
      store.dispatch('TOGGLE_LOADING')
      store.dispatch('TOGGLE_LOADING')
      this.checkUser()
      this.toggleLoading()
    },
    checkUser: function () {
      if (this.username === 'admin' || this.username === 'user') {
        this.$store.dispatch('SET_USER', this.username)
        if (window.localStorage) {
          window.localStorage.setItem('user', this.username)
        }
        this.$router.push('/')
      } else {
        window.alert('user tidak ditemukan')
      }
    },
    toggleLoading: function () {
      this.loading = (this.loading === '') ? 'loading' : ''
    }
  },
  mounted: function () {
    if (window.localStorage) {
      var userItem = window.localStorage.getItem('user')
      if (userItem) {
        this.$router.push('/')
      }
    }
  }
}
</script>

<style>
html, body, .container-table {
  height: 100%;
  background-color: #282B30 !important;
}
.container-table {
    display: table;
    color: white;
}
.vertical-center-row {
    display: table-cell;
    vertical-align: middle;
}
.vertical-20p {
  padding-top: 20%;
}
.vertical-10p {
  padding-top: 10%;
}
.logo {
  width: 15em;
  padding: 3em;
}
.loginForm .input-group {
  padding-bottom: 1em;
  height: 4em;
}
.input-group input {
  height: 4em;
}
</style>



